import React, { Component } from 'react';

class About extends Component {
  render() {
    if (this.props.data) {
      var sectionName = document.documentElement.lang === 'fr' ? 'A propos' : 'About me';
      var hello = document.documentElement.lang === 'fr' ? 'Bonjour' : 'Hello';
      var welcome = document.documentElement.lang === 'fr' ? 'Bienvenue sur mon site web' : 'Welcome on my website';
      var profilepic = 'images/' + this.props.data.image;
      var about = this.props.data.description;
    }

    return (
      <section id="about">
        <div className="col-md-12">
          <h1 style={{ color: 'black' }}>
            <span>{sectionName}</span>
          </h1>
          <div className="row center mx-auto mb-5">
            <div className="col-md-4 mb-5 center">
              <div className="polaroid">
                <span style={{ cursor: 'auto' }}>
                  <img
                    height="auto"
                    src={profilepic}
                    alt="Ludovic DELAVOIS Profile Pic"
                  />
                </span>
              </div>
            </div>

            <div className="col-md-8 center">
              <div className="col-md-10">
                <div className="card">
                  <div className="card-header">
                    <span
                      className="iconify"
                      data-icon="emojione:red-circle"
                      data-inline="false"
                    ></span>{' '}
                    &nbsp;{' '}
                    <span
                      className="iconify"
                      data-icon="twemoji:yellow-circle"
                      data-inline="false"
                    ></span>{' '}
                    &nbsp;{' '}
                    <span
                      className="iconify"
                      data-icon="twemoji:green-circle"
                      data-inline="false"
                    ></span>
                  </div>
                  <div
                    className="card-body font-trebuchet text-justify ml-3 mr-3"
                    style={{
                      height: 'auto',
                      fontSize: '132%',
                      lineHeight: '200%',
                    }}
                  >
                    <br />
                      <span className="wave">{hello}! {welcome}!</span>
                    <br />
                    <br />
                    {about}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;
